.header {
  background: white;
  height: 10vh;
  position: fixed;
  top: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid lightgrey;
  z-index: 100;
  user-select: none;
}

.logo-section {
  padding: 0 2%;
  /* background: lavenderblush; */
  height: 75%;
}

.logo {
  height: 100%;
}

.navlinks-section{
  padding: 0 2%;
}

.navlink{
  /* background: lavenderblush; */
  margin: 0 5px;
  font-size: 16px;
  transition-duration: 0.4s;
}
.navlink:hover{
  text-decoration: underline;
}

.activenavlink{
  transition-duration: 0.4s;
  color: red;
}
